      position: relative;
      overflow: hidden;
      border-radius: 2px;
      box-shadow: var(--shadow-elevate-12); }
      .section-quotes .quotes__item::after {
        content: "\201C";
        position: absolute;
        top: -2.75rem;
        left: -1rem;
        font-size: 20rem;
        line-height: 1;
        color: var(--color-grey-light-2);
        font-family: sans-serif;
        z-index: 1; }
      .section-quotes .quotes__item::before {
        content: "\201D";
        position: absolute;
        bottom: -13rem;
        right: -1rem;
        font-size: 20rem;
        line-height: 1;
        color: var(--color-grey-light-2);
        font-family: sans-serif;
        z-index: 1; }
      .section-quotes .quotes__item__text {
        color: #141414;
        font-size: 1.3rem;
        z-index: 9;
        font-style: italic;
        -webkit-font-smoothing: antialiased; }

.section-questions {
  padding: 2rem; }
  .section-questions .questions {
    margin: 0 auto; }
    .section-questions .questions__item {
      padding: 2rem;
      background-color: #fff;
      display: flex; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto"; }
