.nav {
  width: 100%;
  z-index: 99;
  height: 4rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: fixed;
  top: 0;
  border-bottom: 1px solid transparent;
  will-change: transform;
  transform: translateY(0);
  transition: all 0.5s; }
  .nav a {
    padding: 1rem;
    font-weight: 900;
    text-transform: uppercase; }
  .nav:before {
    content: " ";
    display: block;
    height: 4rem; }
  .nav__links {
    margin-right: auto;
    margin-left: 1rem;
    letter-spacing: 2px; }
    .nav__links > a {
      padding: 1rem 0; }
  .nav--right {
    color: #141414;
    flex: 0 1 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 2rem; }
  .nav__share__links {
    display: flex;
    list-style: none; }
  .nav .scroll-color {
    background-color: #fff; }
  .nav.hidden {
    transform: translateY(-100%); }
  .nav__fix {
    background-color: #fff;
    border-bottom: var(--line);
    box-shadow: var(--shadow-elevate-8); }
  .nav__logo {
    height: 3.5rem;
    margin-left: 2rem; }
